import "react-native-gesture-handler/jestSetup";

// Mock NativeAnimatedHelper pour supprimer les warnings d'animation
import { NativeAnimatedHelper } from 'react-native/Libraries/Animated/NativeAnimatedHelper';
jest.mock('react-native/Libraries/Animated/NativeAnimatedHelper');

// Supprimer les warnings spÃ©cifiques
const originalError = console.error;
console.error = (...args) => {
  if (
    typeof args[0] === "string" &&
    args[0].includes(
      "An update to Animated(View) inside a test was not wrapped in act"
    )
  ) {
    return; // ignore le warning
  }
  originalError(...args);
};
